/* ADVANTAGES */
.advantages {
  display: block;
  list-style: none;
}
.advantages__item {
  position: relative;
  padding-top: 75px;
  width: 330px;
}
.advantages__item:nth-child(n+2) {
  margin-top: 36px;
}
.advantages__item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background-repeat: no-repeat;
  min-width: 33px;
  min-height: 50px;
  background-size: 33px 50px;
  background-image: url("/images/advantages/icon1.png");
}
@media screen and (-webkit-min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
  .advantages__item::after {
    background-image: url("/images/advantages/icon1.png "), url("/images/advantages/icon1@2x.png");
  }
}
.advantages__item:nth-child(2)::after {
  width: 50px;
  height: 50px;
  background-size: 50px 50px;
  background-image: url("/images/advantages/icon2.png");
}
@media screen and (-webkit-min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
  .advantages__item:nth-child(2)::after {
    background-image: url("/images/advantages/icon2.png "), url("/images/advantages/icon2@2x.png");
  }
}
.advantages__item:nth-child(3)::after {
  width: 44px;
  background-size: 44px 50px;
  background-image: url("/images/advantages/icon3.png");
}
@media screen and (-webkit-min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
  .advantages__item:nth-child(3)::after {
    background-image: url("/images/advantages/icon3.png "), url("/images/advantages/icon3@2x.png");
  }
}
.advantages__item:not(:last-child) {
  margin-right: 60px;
}
.advantages__text {
  width: 230px;
